import { InputLabel, MenuItem, Select, SelectChangeEvent } from "@rp/react/web/mui/core"
import { useFeedbackContext } from "./FeedbackContext"
import { FeedbackForm, FormControlStyled } from "./FeedbackMode.styles"

export default function RankingModeForm({ rankingProfiles }: { rankingProfiles: Array<string> }) {
  const { rankingMode, updateQueryRanking } = useFeedbackContext()

  const handleChange = (event: SelectChangeEvent) => {
    updateQueryRanking(event.target.value)
  }

  const options =
    rankingProfiles &&
    rankingProfiles.map(option => (
      <MenuItem key={option} value={option}>
        {option}
      </MenuItem>
    ))

  return (
    <FeedbackForm>
      <FormControlStyled>
        <InputLabel id="ranking-label">Ranking</InputLabel>
        <Select
          data-testid="ranking-profile"
          value={rankingMode}
          label="ranking"
          onChange={handleChange}
          inputProps={{
            "data-testid": "options-id",
          }}
        >
          {options}
        </Select>
      </FormControlStyled>
    </FeedbackForm>
  )
}
